
function formatTime (current) {
    let minutesCorrect = (current.getMinutes()<10?'0':'') + current.getMinutes();
    let formattedTime =
        current.getDate() + "." + 
        (current.getMonth()+1) + "." + 
        current.getFullYear() + " " +
        current.getHours() + ":" +
        minutesCorrect
    return formattedTime;
}

export default { formatTime }